			"id": 232,
			"name": "Newborn Baby Fruit Shape Silicone Teether (Saver Pack of 3) ",
			"hindi_name": "नियूबोर्न  बेबी  फ्रूट शेप  सिलिकॉन  तीथर  ( पैक ऑफ़ 3) ",
			"description": "<p><strong>Features &amp; details</strong></p>\r\n\r\n<ul>\r\n\t<li>Soft silicone teether, BPA Free easily chew-able and small attractive shape for newborn baby.</li>\r\n\t<li>Textured surface has a massage-like feel on the gums when baby chews on the teether.</li>\r\n\t<li>Easy to grip the silicone teether; designed for newborn hands.</li>\r\n\t<li>It comes in a box to keep the teether clean when not in use.</li>\r\n\t<li>Design may vary</li>\r\n\t<li>Great for 3 months to 1 year of baby.&nbsp;</li>\r\n</ul>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<p><strong>Description&nbsp;</strong></p>\r\n\r\n<p>This Baby Fruit Shape Teether Toy is made of food-grade silicone and ABS environmental material and comes in Set of Apple, Strawberry &amp; Grapes. The bright colors of this teether are good for your baby&#39;s perception of color. Objects of different shapes help the baby to recognize and remember the world around him. With thoughtfully-designed buckle for hassle-free grasping and lightweight construction to eliminate any strain, this silicone teething set can be easily used by babies and helps develop their fine motor skills and dexterity as they grab the teether toys.</p>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<p><strong>Product information&nbsp;</strong></p>\r\n\r\n<table border=\"0\" cellpadding=\"0\" cellspacing=\"0\">\r\n\t<tbody>\r\n\t\t<tr>\r\n\t\t\t<td>Batteries required</td>\r\n\t\t\t<td>No</td>\r\n\t\t</tr>\r\n\t\t<tr>\r\n\t\t\t<td>Item Weight</td>\r\n\t\t\t<td>399 g</td>\r\n\t\t</tr>\r\n\t\t<tr>\r\n\t\t\t<td>Package Dimensions</td>\r\n\t\t\t<td>10 x 6 x 4 cm</td>\r\n\t\t</tr>\r\n\t\t<tr>\r\n\t\t\t<td>Manufacturer&#39;s Minimum Suggested Age(months)</td>\r\n\t\t\t<td>3</td>\r\n\t\t</tr>\r\n\t</tbody>\r\n</table>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<p>&nbsp;</p>\r\n",
			"hindi_description": "<p>विवरण</p>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<p>यह बेबी फ्रूट शेप टेथर टॉय फूड-ग्रेड सिलिकॉन और ABS पर्यावरण सामग्री से बना है और सेट ऑफ एप्पल, स्ट्रॉबेरी और अंगूर में आता है। इस टीथर के चमकीले रंग आपके बच्चे की रंग की धारणा के लिए अच्छे हैं। विभिन्न आकृतियों की वस्तुएं बच्चे को उसके आसपास की दुनिया को पहचानने और याद रखने में मदद करती हैं। किसी भी तनाव को खत्म करने के लिए परेशानी से मुक्त लोभी और हल्के निर्माण के लिए सोच-समझकर बकसुआ के साथ, यह सिलिकॉन शुरुआती सेट आसानी से शिशुओं द्वारा उपयोग किया जा सकता है और अपने ठीक मोटर कौशल और निपुणता को विकसित करने में मदद करता है क्योंकि वे टीथर के खिलौने को हड़प लेते हैं।</p>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<p>शीतल सिलिकॉन टीथर, BPA फ्री आसानी से चबाने में सक्षम और नवजात बच्चे के लिए छोटे आकर्षक आकार।<br />\r\nबनावट वाली सतह पर मसूड़ों पर मालिश जैसी अनुभूति होती है जब बच्चा टीथर पर चबाता है।<br />\r\nसिलिकॉन टेथर को पकड़ना आसान; नवजात हाथों के लिए डिज़ाइन किया गया।<br />\r\nयह एक बॉक्स में आता है जब उपयोग में नहीं होने पर टेथर को साफ रखने के लिए।<br />\r\nडिजाइन भिन्न हो सकते हैं<br />\r\n3 महीने से 1 वर्ष के बच्चे के लिए बढ़िया है!</p>\r\n",
