			"hindi_description": "<p><span style=\"color:black\">प्रोडक्ट<strong> </strong>की<strong> </strong>जानकारी</span></p>\r\n\r\n<ul>\r\n\t<li><span style=\"color:black\">टाइप : फ्लेक्सबल थर्मामीटर</span></li>\r\n\t<li><span style=\"color:black\">प्रयोग : बच्चे और वयस्कों के लिए</span></li>\r\n\t<li><span style=\"color:black\">माप श्रेणी : (32.0 ~ 42.0) &deg; C / (89.6 ~ 107.6) &deg; F</span></li>\r\n\t<li><span style=\"color:black\">माप की सटीकता : &plusmn; 0.1 &deg; F / &deg; 0.2 &deg; F</span></li>\r\n\t<li><span style=\"color:black\">न्यूनतम पैमाना : 0.1 डिग्री सेल्सियस / 0.1 &deg; एफ</span></li>\r\n\t<li><span style=\"color:black\">बैटरी का प्रकार : लिथियम बैटरी, प्रकार सीआर 1220, 3 वी</span></li>\r\n\t<li><span style=\"color:black\">प्रतिक्रिया : दस सैकेंड</span></li>\r\n\t<li><span style=\"color:black\">एलसीडी साइज : 3.1 सेमी X 1.4 सेमी</span></li>\r\n\t<li><span style=\"color:black\">मैमोरी : लास्ट रीडिंग</span></li>\r\n\t<li><span style=\"color:black\">परिणाम आ जाने पर अलार्म बजता है : हाँ</span></li>\r\n\t<li><span style=\"color:black\">ऑटो स्विच ऑफ : हाँ</span></li>\r\n\t<li><span style=\"color:black\">वॉटरप्रूफ : हाँ</span></li>\r\n\t<li><span style=\"color:black\">प्रोडक्ट का वज़न : 18.8 ग्राम</span></li>\r\n\t<li><span style=\"color:black\">प्रोडक्ट का माप : 14.3cm X 2.4cm X 1.55cm&nbsp;</span></li>\r\n</ul>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<p><span style=\"color:black\">विवरण</span></p>\r\n\r\n<p><span style=\"color:black\">डिजिटल थर्मामीटर एक ऐसा थर्मामीटर है जिस पर तापमान एक एलसीडी स्क्रीन पर दिखाई देता है यह बैटरी से चलने वाला उपकरण है इस थर्मामीटर के अंदर सेंसर होते है जिससे तापमान का पता लगाया जाता है. डिजिटल थर्मामीटर प्लास्टिक के बने होते है और एक दम सही तापमान बताते है.डिजिटल थर्मामीटर का उपयोग करना बहुत ही आसान है इससे आप कुछ ही सेकंड्स में टेम्परेचर का पता कर सकते </span></p>\r\n\r\n<p><span style=\"color:black\">सुविधाएँ<strong> </strong>और<strong> </strong>विवरण</span></p>\r\n\r\n<ul>\r\n\t<li><span style=\"color:black\">वयस्कों द्वारा उपयोग किया जा सकता है, लेकिन बच्चों कि पहुंच से दूर रखें</span></li>\r\n\t<li><span style=\"color:black\">यह स्टोरेज केस के साथ उपलब्ध है जो इसके हाइजीनिक होने का पूरा ध्यान रखता है.</span></li>\r\n\t<li><span style=\"color:black\">60 सेंकेड में रीडआउट करता है</span></li>\r\n\t<li><span style=\"color:black\">लिक्विड क्रिस्टल डिस्प्ले</span></li>\r\n\t<li><span style=\"color:black\">मेमोरी रिकॉर्ड</span></li>\r\n\t<li><span style=\"color:black\">सुरक्षित, पारा मुक्त</span></li>\r\n\t<li><span style=\"color:black\">एक्युरिट 0.2 &deg; F पर सटीक</span></li>\r\n\t<li><span style=\"color:black\">परिणाम आ जाने पर अलार्म बजता है</span></li>\r\n\t<li><span style=\"color:black\">ऑटो स्विच ऑफ</span></li>\r\n</ul>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<p><span style=\"color:black\">डिजिटल<strong> </strong>थर्मामीटर<strong> </strong>का<strong> </strong>इस्तेमाल<strong> </strong>करते<strong> </strong>वक्त<strong> </strong>इन<strong> </strong>बातों<strong> </strong>का<strong> </strong>ख्याल<strong> </strong>रखें</span></p>\r\n\r\n<ul>\r\n\t<li><span style=\"color:black\">बच्चों को इसके बैटरी कवर को हटाने व फाड़ने से रोकें</span></li>\r\n\t<li><span style=\"color:black\">धूप या गंदगी से डिजिटल थर्मामीटर को दूर रखें</span></li>\r\n\t<li><span style=\"color:black\">मुहं से तापमान लेने पर सेंसर टिप को काटे नहीं</span></li>\r\n\t<li><span style=\"color:black\">किसी वयस्क निगरानी के तहत ही इसका इस्तेमाल करें</span></li>\r\n\t<li><span style=\"color:black\">बच्चों की पहुंच से दूर रखें</span></li>\r\n\t<li><span style=\"color:black\">तापमान मापने के लिए डिजिटल थर्मामीटर की टिप को जीभ के नीचे रखें</span></li>\r\n\t<li><span style=\"color:black\">इस्तेमाल करने से पहले इसके ऊपर दी गी निर्देशों को ध्यान से पढ़े</span></li>\r\n</ul>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<p><span style=\"color:black\">इस्तेमाल<strong> </strong>करने<strong> </strong>के<strong> </strong>लिए<strong> </strong>निर्देश</span></p>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<ul>\r\n\t<li><span style=\"color:black\">डिजिटल थर्मामीटर को रबिंग एल्कोहल या फिर पानी से साफ करें</span></li>\r\n\t<li><span style=\"color:black\">इसके बाद, मुहं खोलें और इसकी टिप को जीभ के नीचे रखें</span></li>\r\n\t<li><span style=\"color:black\">अपने होठों से थर्मामीटर को हल्के से दबाते हुए बंद कर लें</span></li>\r\n\t<li><span style=\"color:black\">जीभ के नीचे थर्मामीटर को दो मिनट तक रखें</span></li>\r\n\t<li><span style=\"color:black\">फिर थर्मामीटर निकालें और तापमान देखें</span></li>\r\n</ul>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<ul>\r\n</ul>\r\n",
